import { getOS } from './platform'
import { DEEPLINK_BASE_URL } from '../constants/constants'

const handleDapplinks = function () {
  const platform = getOS()
  switch (platform) {
    case 'iOS':
    case 'Android':
      if (window.location.search.indexOf('?deeplink') >= 0) {
        window.location.href =
          DEEPLINK_BASE_URL + '/' + window.location.search.split('deeplink=')[1]
      }
      break
    default:
      if (window.location.search.length > 0) {
        window.location.href =
          window.location.origin +
          '/' +
          window.location.search.split('deeplink=')[1]
      }
      break
  }
}

export default handleDapplinks
